<template>
  <div id="nav">
    <template v-if="showSearch">
      <div
        @click="
          $router.push({
            path: '/search',
          })
        "
      >
        <van-search disabled :background="bgColor" v-if="showSearch" />
        <!-- 轮播图 -->
        <van-swipe
          v-if="showSearch"
          class="my-van-swipe"
          style="height: 30px"
          vertical
          :autoplay="4000"
          :show-indicators="false"
          :loop="true"
          :touchable="false"
        >
          <template v-if="searchWords">
            <van-swipe-item
              v-for="(item, index) in searchWords.slice(0, 5)"
              :key="index"
            >
              {{ item }}
            </van-swipe-item>
          </template>
        </van-swipe>
      </div>
    </template>

    <van-nav-bar :style="`background:${bgColor}`">
      <template #left>
        <router-link to="/">推荐</router-link>
        <router-link to="/boy">男生</router-link>
        <router-link to="/girl">女生</router-link>
        <router-link to="/publish">出版</router-link>
      </template>
      <template #right v-if="!showSearch">
        <van-icon
          name="search"
          size="14"
          @click="$router.push({ path: '/search' })"
        />
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchWords: [], //轮播查询词
      scrollTopY: 0, //页面滚动的距离
      showSearch: true, //是否在展示当前的input框
      bgColor: "#2f4464", //背景颜色
    };
  },
  watch: {
    scrollTopY(n) {
      // 如果当前的scroll小于50那么就隐藏input框
      if (n <= 50) {
        this.showSearch = true;
      } else {
        this.showSearch = false;
      }
    },
    // 查询当前的路由名字,根据当前路由修改当前的导航栏颜色
    "$route.name"(n) {
      // console.log(n);
      switch (n) {
        case "Recommend":
          this.bgColor = "#2f4464";
          break;
        case "Boy":
          this.bgColor = "#3F5D7D";
          break;
        case "Girl":
          this.bgColor = "rgb(243,114,111)";
          break;
        case "Publish":
          this.bgColor = "rgb(159,125,80)";
          break;
      }
    },
  },
  created() {
    // 查询当前的查询词
    this.axios.get("/rank/54d42d92321052167dfb75e3").then((response) => {
      this.searchWords = response.data.ranking.books
        .slice(0, 10)
        .map((item) => {
          return item.title;
        });
    });
    this.watchRoute();
  },
  mounted() {
    // 监听当前页面的上下滚动距离
    window.addEventListener("scroll", () => {
      this.scrollTopY = document.documentElement.scrollTop;
    });
  },
  methods: {
    // 查询当前的路由名字,根据当前路由修改当前的导航栏颜色
    watchRoute() {
      switch (this.$route.name) {
        case "Recommend":
          this.bgColor = "#2f4464";
          break;
        case "Boy":
          this.bgColor = "#3F5D7D";
          break;
        case "Girl":
          this.bgColor = "rgb(243,114,111)";
          break;
        case "Publish":
          this.bgColor = "rgb(159,125,80)";
          break;
      }
    },
  },
};
</script>

<style lang="less">
// .bgColor {
//   background: #2f4464;
// }
#nav {
  position: relative;
  z-index: 20;
  position: fixed;
  top: 0;
  width: 100vw;
  .van-search {
    padding-bottom: 0;
    // .bgColor();
    .van-search__content {
      .van-cell {
        color: #fff;
      }
      border-radius: 8px;
      // background: rgb(114 152 160 / 20%);
      background: rgba(114, 152, 160, 0.2);
    }
  }
  .my-van-swipe {
    position: absolute;
    top: 17px;
    left: 50px;
    width: 300px;
    font-size: 14px;
    color: #fff;
    .van-swipe-item {
      min-height: 30px;
    }
  }
  .van-nav-bar {
    min-height: 40px;
    // .bgColor();
    .van-nav-bar__content {
      height: 40px;
      // height: 60px;
      .van-nav-bar__left {
        width: 100%;
        height: 40px;
        // height: 60px;
        align-items: center;
        &:active {
          opacity: 1;
        }
        a {
          display: inline-block;
          width: 25%;
          color: #fff;
          margin-right: 25px;
        }
        .router-link-exact-active {
          font-size: 17px;
          position: relative;
          &::after {
            position: absolute;
            content: "";
            display: block;
            left: 25%;
            bottom: -6px;
            width: 50%;
            height: 4px;
            border-radius: 2px;
            background: #fff;
          }
        }
      }
      .van-nav-bar__right {
        width: 40px;
        .van-icon {
          color: #fff;
        }
      }
    }
  }
}
</style>