<template>
  <div @touchstart="movingStart" @touchmove="movingMove" @touchend="movingEnd">
    <HomeNav />

    <router-view></router-view>
  </div>
</template>

<script>
import HomeNav from "@/components/HomeNav.vue";

export default {
  components: {
    HomeNav,
  },
  data() {
    return {
      moveState: false,
      movestartX: null,
      moveX: null,
    };
  },
  methods: {
    // 获取开始移动的x坐标
    movingStart(e) {
      this.movestartX = e.touches[0].clientX;
      this.moveState = true;
      //   console.log("开始", this.movestartX);
    },
    // 获取移动的x坐标
    movingMove(e) {
      if (this.moveState) {
        this.moveX = e.touches[0].clientX;
        // console.log("移动", this.moveX);
      }
    },
    movingEnd() {
      this.moveState = false;
      //   console.log("结束", this.movestartX, this.moveX);
      if (this.movestartX == null || this.moveX == null) {
        return;
      }
      let name = this.$route.name;
      switch (name) {
        case "Recommend":
          if (this.moveX < this.movestartX - 100) {
            this.$router.push({
              path: "/boy",
            });
          }
          break;
        case "Boy":
          if (this.moveX < this.movestartX - 100) {
            this.$router.push({
              path: "/girl",
            });
          } else if (this.movestartX < this.moveX - 100) {
            this.$router.push({
              path: "/",
            });
          }
          break;
        case "Girl":
          if (this.moveX < this.movestartX - 100) {
            this.$router.push({
              path: "/publish",
            });
          } else if (this.movestartX < this.moveX - 100) {
            this.$router.push({
              path: "/boy",
            });
          }
          break;
        case "Publish":
          if (this.movestartX < this.moveX - 100) {
            this.$router.push({
              path: "/girl",
            });
          }
          break;
      }
      this.movestartX = null;
      this.moveX = null;
    },
  },
};
</script>

